"use strict";


/**
 * ä»base64æ°æ®ä¸­åå»ºFileå¯¹è±¡.
 * @param {*} base64Data 
 */
export function getFileByBase64(base64Data, fileName) {
  //å°base64è½¬æ¢ä¸ºblob
  var arr = base64Data.split(','),
      mime = arr[0].match(/:(.*?);/)[1],
      bstr = atob(arr[1]),
      n = bstr.length,
      u8arr = new Uint8Array(n);
  while (n--) {
      u8arr[n] = bstr.charCodeAt(n);
  }
  let blob = new Blob([u8arr], { type: mime });
  
  //å°blobè½¬æ¢ä¸ºfile
  blob.lastModifiedDate = new Date();
  blob.name = fileName || 'image';
  return blob;
}

/**
 * @param file Fileå¯¹è±¡.
 * @param callback (base64Data)=>void
 */
export function getImageBase64ByFile(file, callback) {
  var reader = new FileReader();
  reader.readAsDataURL(file);
  reader.onload = function () {
    callback(reader.result);
  }
}

/**
 * @param fileInput æä»¶çinput.
 * @param callback (base64Data, width, height)=>void
 * @param outputFormat ? å¾çæ ¼å¼
 */
export function getImageBase64(fileInput, callback, outputFormat) {
  var url;
  if (navigator.userAgent.indexOf("MSIE") >= 1) {
    // IE
    url = fileInput.value;
  } else if (navigator.userAgent.indexOf("Firefox") > 0) {
    // Firefox
    url = window.URL.createObjectURL(fileInput.files.item(0));
  } else if (navigator.userAgent.indexOf("Chrome") > 0) {
    // Chrome
    url = window.URL.createObjectURL(fileInput.files.item(0));
  } else {
    url = window.URL.createObjectURL(fileInput.files.item(0));
  }

  if (!url) {
    callback.call(this, null);
    return;
  }

  var canvas = document.createElement("canvas"),
    ctx = canvas.getContext("2d"),
    img = new Image();
  img.crossOrigin = "Anonymous";
  img.onload = function() {
    canvas.height = img.height;
    canvas.width = img.width;
    ctx.drawImage(img, 0, 0);
    var dataURL = canvas.toDataURL(outputFormat || "image/png");
    callback.call(this, dataURL, img.width, img.height);
    canvas = null;
  };
  img.src = url;
}

/**
 * å¾çåç¼©
 * @param {*} base64Data 
 * @param {*} maxSize æå¤§çå®½æé«çå°ºå¯¸, 0åä¸º1024;
 * @param {*} callback (base64Data, width, height)=>void
 */
export function compressImage(base64Data, maxSize, outputFormat, callback) {
  maxSize = maxSize > 0 ? maxSize : 1024
  let maxW = maxSize;
  let maxH = maxSize;
  const image = new Image();
  image.addEventListener("load", function(e) {
    let ratio; //å¾ççåç¼©æ¯
    let needCompress = false; // æ¯å¦éè¦åç¼©
    if (maxW < image.naturalWidth) {
      needCompress = true;
      ratio = image.naturalWidth / maxW;
      maxH = image.naturalHeight / ratio;
    }
    //ç»è¿å¤çåï¼å®éå¾ççå°ºå¯¸ä¸º1024 * 640;
    if (maxH < image.naturalHeight) {
      needCompress = true;
      ratio = image.naturalHeight / maxH;
      maxW = image.naturalWidth / ratio;
    }
    if (!needCompress) {
      maxW = image.naturalWidth;
      maxH = image.naturalHeight;
      callback && callback(base64Data, maxW, maxH);
      return;
    } //å¦æä¸éè¦åç¼©ï¼éè¦è·åå¾ççå®éå°ºå¯¸

    let canvas = document.createElement("canvas");
    canvas.width = maxW;
    canvas.height = maxH;

    const ctx = canvas.getContext("2d");
    ctx.clearRect(0, 0, maxW, maxH);
    ctx.drawImage(image, 0, 0, maxW, maxH);
    // toDataURLçç¬¬äºä¸ªåæ°è¡¨ç¤ºå¾çè´¨éï¼åå¼ä¸º0-1 1ä¸ºåå¾
    const compressImage = canvas.toDataURL(outputFormat||"image/png");
    canvas = null;
    callback && callback(compressImage, maxW, maxH);
  });
  image.src = base64Data;
}
